*{font-family:sans-serif;box-sizing:border-box}.loading{pointer-events:none;filter: brightness(0.65)}button,input{font-size:20px;padding:10px;border-radius:5px}input{display:block;margin-bottom:20px}button{border:1px solid gray;background:whitesmoke;margin-right:5px}button:nth-last-child(1){background:rgb(220, 143, 2);color:white;font-weight:bold}.calc-wrapper{background-color:#222;min-width:235px}.calc-display{padding:10px;position:relative;max-width:400px}.calc-display-signindicator{position:absolute;left:10px;top:10px;background:transparent;text-align:left;color:#ddd;border:none;pointer-events:none}.calc-display-input{width:100%;text-align:right;font-family:'Courier New', Courier, monospace;background-color:#444;color:#fff;margin-bottom:5px;border:1px solid #ddd2;border-radius:8px;box-shadow:inset #2224 0 5px 3px 1px}.calc-display-inputstate{width:100%;text-align:right;font-family:'Courier New', Courier, monospace;background-color:#333;color:#fff;padding:0;margin-bottom:5px;border:none;pointer-events:none;border-radius:8px;border:1px solid transparent;padding:4px 10px 2px 0}.calc-keyboard{background-color:#222;padding-bottom:10px}.calc-keyboard-button:hover{filter: brightness(1.2)}.calc-keyboard-button:focus{outline:3px solid #fff6}.calc-keyboard-button{font-size:1.5em;font-family:'Roboto', 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;border-radius:50%;background-color:#444;color:#fff;min-height:50px;min-width:50px;cursor:pointer;border:1px solid #2224;line-height:0.8em;box-shadow:inset #2224 0 5px 3px 1px}.calc-keyboard-button--orange{background-color:rgb(255, 195, 85);color:#222}.hideme{visibility:hidden}.calc-keyboard-row{padding:0 0 10px 10px}
